import React, { useState } from "react";
import { Col, Container, Row } from "react-bootstrap";
import Input from "@mui/material/Input";
import { TextField } from "@mui/material";
import styles from "./style.module.scss";
const ReceivePerson = ({ ShowNext, display = "block" }) => {
  // {ShowNext}
  const [personData, setPersonData] = useState({ name: "", person: "" });
  const [personList, setPersonList] = useState([
    {
      name: "dad",
      click: true,
      img: "/images/Letter/Receive/dad.png",
      hoverImg: "/images/Letter/choose.png",
    },
    {
      name: "mom",
      click: true,
      img: "/images/Letter/Receive/mom.png",
      hoverImg: "/images/Letter/choose.png",
    },
    {
      name: "grandpa",
      click: true,
      img: "/images/Letter/Receive/grandpa.png",
      hoverImg: "/images/Letter/choose.png",
    },
    {
      name: "grandma",
      click: true,
      img: "/images/Letter/Receive/grandma.png",
      hoverImg: "/images/Letter/choose.png",
    },
  ]);

  const SetClick = (name, index, reset = false) => {
    if (reset) {
      setPersonList(
        personList.map((x) => {
          return x.name === name ? { ...x, click: false } : { ...x };
        })
      );
    } else {
      setPersonList(
        personList.map((x) => {
          return x.name === name
            ? { ...x, click: true }
            : { ...x, click: false };
        })
      );
      setPersonData({ ...personData, person: personList[index].name });
    }
  };

  return (
    <Container
      fluid
      style={{ display: `${display}`, padding: 0, position: "relative" }}
    >
      <img
        style={{ width: "100%" }}
        src={process.env.PUBLIC_URL + "/images/Letter/Receive/back.png"}
        alt="back"
      />
      <div className={styles.ReceiveMom}>
        <img
          src={process.env.PUBLIC_URL + "/images/Letter/Receive/mom.png"}
          alt="mom"
          onClick={() => SetClick("mom", 0)}
        />
      </div>
      <div className={styles.ReceiveDad}>
        <img
          src={process.env.PUBLIC_URL + "/images/Letter/Receive/dad.png"}
          alt="dad"
          onClick={() => SetClick("dad", 1)}
        />
      </div>
      <div className={styles.ReceiveGrandPa}>
        <img
          src={process.env.PUBLIC_URL + "/images/Letter/Receive/grandpa.png"}
          alt="grandpa"
          onClick={() => SetClick("grandpa", 2)}
        />
      </div>
      <div className={styles.ReceiveGrandMa}>
        <img
          src={process.env.PUBLIC_URL + "/images/Letter/Receive/grandma.png"}
          alt="grandma"
          onClick={() => SetClick("grandma", 3)}
        />
      </div>
      <div className={styles.ReceiveType}>
        <img
          src={process.env.PUBLIC_URL + "/images/Letter/Receive/name.png"}
          alt="back"
        />
      </div>
      <div className={styles.textStyle}>
        {/* <TextField size="medium" id="standard-basic" label="名字" variant="standard" /> */}
        <Input
          placeholder="名字"
          style={{ width: "inherit", fontSize: "2vh", textAlign: "center" }}
          onChange={(e) =>
            setPersonData({ ...personData, name: e.target.value })
          }
        />
      </div>
      <div
        className={styles.clickStyle}
        onClick={() => ShowNext(personData.name, personData.person)}
      />
    </Container>
  );
};

export default ReceivePerson;
